.app.dark {
    background-color: #111111;
    color: rgb(156 156 156);

    .navbarContainer {
        background-color: #111111;
        color: rgb(156 156 156);
        border-bottom: 0.5px solid rgb(156 156 156);

        .logo {
            color: rgb(156 156 156);

        }

        .navbarLink {
            color: rgb(156 156 156);

        }

        .searchInput {
            color: #000000;
        }

    }

    .sidebar {
        background-color: #111111;
        color: rgb(156 156 156);

        .sidebarButton {
            background-color: rgb(156 156 156);
            color: #000000;

        }
    }

    .menuLink:hover {
        background-color: rgb(98, 97, 97, 0.3);
    }

    .sidebarFriendName {
        color: rgb(156 156 156);
    }

    .feed {
        background-color: #111111;
        color: rgb(156 156 156);

        .shareInput {
            color: rgb(156 156 156)
        }
    }
    .share{
        .shareWrapper{
            .shareTop{
                .shareInput{
                    color:rgb(156,156,156)

                }
            }
        }
    }

    .commentBox {
        display: flex;

        .commentInput {
            color: rgb(156 156 156);
            border: 1px solid rgb(156 156 156);
        }

        .commentPost {
            background-color: rgb(156 156 156);
            color: #000000;
        }
    }

    .comment {
        .commentWrapper {
            border-top: 1px dashed rgb(156 156 156);
            .commentProfileImg{
                border:2px solid rgb(156 156 156);
            }

            .commentInfo{
                .commentText{
                    background-color: rgb(156 156 156);
                    color:#000000;
                }
            }
        }
    }

    .rightbar {
        background-color: #111111;

        .rightbarUserName {
            color: rgb(156 156 156);
        }
    }

    .editButton {
        background-color: rgb(156 156 156);
        color: #000000;
    }

    .editProfile {
        .bottom {
            .right {
                form {
                    background-color: #111111;
                    border: 2px solid #111111;

                    input {
                        color: rgb(156 156 156);
                    }

                    .updateButton {
                        color: #111111;
                        background-color: rgb(156 156 156);
                    }
                }
            }
        }
    }
}