form {
    border: 2px solid #ffffff;
    background-color: #ffffff;
    width: 400px;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 2px 4px rgba(0, 0, 0, .1), 0 8px 16px rgba (0, 0, 0, .1);

}

.login {
    width: 80%;
}

.container {
    background-color: #f1f2f6;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    width: 100%;
    padding: 50px;
}

.facebook {
    width: 60%;
    display: flex;
    justify-content: center;
    flex-direction: column;

}

.loginContainer {
    width: 40%;
}

.inputAction input {
    width: 100%;
    margin-top: 25px;
    height: 50px;
    padding: 5px;
    font-size: 20px;
    outline: none;
}

.loginbtn button {
    width: 100%;
    margin-top: 25px;
    height: 50px;
    border-radius: 5px;
    border: none;
    font-size: 20px;
    cursor: pointer;

}

.forgetPssword span {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
    font-size: 18px;
    cursor: pointer;
}

.createbtn {
    display: flex;
    justify-content: center;
    align-items: center;

}

.createbtn button {
    padding: 20px;
    font-size: 20px;
    border-radius: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
    border: none;
    cursor: pointer;


}

.createbtn a {
    text-decoration: none;
}

.empty {
    border-bottom: 2px solid #eeeeee;
    margin-top: 20px;
}

.createpage p {
    margin-top: 20px;
    display: flex;
    /* justify-content: center; */
    align-items: center;
}

.facebook .book {
    font-size: 70px;


}

.books{
    font-size: 70px;
    background-color: none;
    display:none;


}

.facebook p {
    font-size: 40px;
    font-weight: 600;
    color: #171811;
}

@media only screen and (max-width:576px) {
    .container {
        background-color: none;
    }

    .facebook {
        display: none;

    }
    .books{
        display: block;
        font-size: 50px;
    }

    .loginContainer {
        /* flex: 1; */
        width: 100%;



    }

    .loginContainer form {
        display: block;
        width: 100%;
     

    }
         

    .inputAction input {
        width: 100%;

    }

   .inputAction input::placeholder {
       font-size: 18px;
   }


    .createbtn button {
        width: 90%;
        padding: 20px;
        font-size: 16px;



    }

    .createbtn {
        display: flex;
        justify-content: center;
        align-items: center;

    }


}

@media only screen and (max-width:768px) {
    

    .loginContainer {
        /* flex: 1; */
        width: 100%;
}

    .loginContainer form {
        display: block;
        width: 100%;
 }
   .books {
       display: block;
       font-size: 50px;
   }

 .inputAction input {
        width: 100%;
 }
 .inputAction input::placeholder {
        font-size: 25px;
    }
     .createbtn button {
        width: 90%;
        padding: 20px;
        font-size: 16px;

 }

 


}
@media only screen and (min-width: 768px) and (max-width: 1200px) {
       facebook {
                display: block;
        
            } 
        
            .loginContainer {
    
                width: 100%;
            }
        
            .loginContainer form {
                display: block;
                width: 100%;
            }
        
            .inputAction input {
                width: 100%;
            }
        
            .inputAction input::placeholder {
                font-size: 25px;
            }
        
            .createbtn button {
                width: 90%;
                padding: 20px;
                font-size: 16px;
        
            }
            .books{
                display:none;
            }

}