.share{
    width:100%;
    height:170%;
    border-radius: 10px;
    -webkit-box-shadow:2px 4px 10px 1px rgba(0,0,0,0.47);
    box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.47);

    .shareWrapper{
        display: flex;
        justify-content: center;
        flex-direction: column;
        padding: 20px;
    }
    .shareTop{
        display:flex;
        align-items: center;
    }
    .shareProfileImg{
        width:50px;
        height:50px;
        border-radius: 50%;
        margin-right: 20px;
        object-fit: cover;
        

    }
    .shareInput{
        border:none;
        background: transparent;
        width:80%;
        font-size: 18px;
        height:30px;
        &:focus{
            outline: none;
        }
    }
    .shareHr{
        margin: 10px;

    }
    .shareImgContainer{
        padding:0px 10px 10px 20px;
        position:relative;

        .shareImg{
            width:100%;
            // height:700px;
            object-fit: contain;
        }
        .shareCancelImg{

            position:absolute;
            top:0;
            right:20px;
            background-color: white;
            padding: 5px;
            border-radius: 50%;
            opacity: 0.8;
            cursor: pointer;
        }
    }
    .shareBottom{
        display:flex;
        align-items: center;
        justify-self: space-between;

        .shareOptions {
            display:flex;
            align-items: center;
            justify-content: space-around;
            margin-left: 20px;
            width:90%;


            .shareOption {
                display:flex;
                align-items: center;
                margin-right: 20px;
                cursor: pointer;
                .shareIcon{
                    font-size: 30px;
                    margin-right: 10px;
                }
                .shareOptionText{
                    font-size: 14px;
                    font-weight: 600;
                }

            }
        }
       
        

    }
         .emoji {
             position: relative;
             padding: 0 20px 10px 20px;
             width: 100%;
             top: 20px;
             left: 70px;
         }
    
}