  .sidebarFriend {
      display: flex;
      align-items: center;
      margin-bottom: 20px;
      cursor: pointer;
  }

  .sidebarFriendImg {
      width: 40px;
      height: 40px;
      border-radius: 50%;
      margin-right: 20px;
      object-fit: cover;
  }

  .sidebarFriendName {
      font-weight: 600;
      color: black;
  }

  //Responsiveness
  @media only screen and (max-width:992px) {
    .sidebarFriendName{
        display:none;
    }


  }